header {
    position: unset;
}

.loginAdmin {
    min-height: 80vh;
    padding-top: 25vh;
    justify-content: center;
    text-align: center;
    font-family: 'Manrope', sans-serif;

}

.loginClient {
    min-height: 100vh;
    display: flex;
    flex-wrap: wrap;
    padding-top: 52px;
    justify-content: center;
    background-attachment: fixed;
    background-size: cover;
    background-position: center;
}

.loginClient input {
    padding: 12px 24px;
    border-radius: 24px;
}

.loginClient input:last-of-type {
    margin-top: 24px;
}

.loginClient h1 {
    margin-top: 72px;
    font-family: 'Raleway', sans-serif;
    font-size: 9em;
    font-weight: 200;
}

.loginClient .login_btn {
    display: flex;
    margin: 42px auto 0 auto;
    padding: 12px 46px;
    font-size: 1.25em;
    background-color: rgb(117, 100, 89);
    border-radius: 32px;
    color: white;
    border: none;
    font-family: 'Raleway', sans-serif;
}

.loginClient form a {
    display: flex;
    justify-content: center;
    padding: 24px;
    color: black;
}

@media(max-width:992px) {
    .loginClient h1 {
        margin-top: 48px;
        font-size: 6em;
    }
}